//memberlist.scss
.profile_img{
    display: block;
    width: 40px;
    height: 40px;
    margin: 10px 15px;
    border-radius: 50%;
    background-size: contain;
}
.profile_name{
    flex: auto;
}

.profile_messages{
    margin-right: 15px;
    padding: 0 10px;
    height: 30px;
    border-radius: 5px;
    background: #f0f0f0;
    font-size: 10px;
    line-height: 30px;
    color: #8e9399;
}